export const MajorArcana = [
  {img: 'fool', link: 'door_1'},
  {img: 'magician', link: 'door_2'},
  {img: 'highpriestess', link: 'door_3'},
  {img: 'empress', link: 'door_4'},
  {img: 'emperor', link: 'door_5'},
  {img: 'hierophant', link: 'door_6'},
  {img: 'lovers', link: 'door_7'},
  {img: 'chariot', link: 'door_8'},
  {img: 'strength', link: 'door_9'},
  {img: 'hermit', link: 'door_10'},
  {img: 'wheeloffortune', link: 'door_11'},
  {img: 'justice', link: 'door_12'},
  {img: 'hangedman', link: 'door_13'},
  {img: 'death', link: 'door_14'},
  {img: 'temperance', link: 'door_15'},
  {img: 'devil', link: 'door_16'},
  {img: 'tower', link: 'door_17'},
  {img: 'star', link: 'door_18'},
  {img: 'moon', link: 'door_19'},
  {img: 'sun', link: 'door_20'},
  {img: 'judgement', link: 'door_21'},
  {img: 'world', link: 'door_22'},
];

const formatInt = (i) => ('0' + i).slice (-2);
const doorGenerator = (first, i) => `door_${first + i}`;
const doorGeneratorReverse = (first, i) => `door_${first + 13 - i}`;
const generateMinors = (baseName, firstDoor, doorGenerator) => {
  return new Array(14).fill({}).map((obj, i) => ({
    img: `${baseName}-${formatInt(i + 1)}`,
    link: doorGenerator(firstDoor, i)
  }));
};

export const Cups = generateMinors('cups', 23, doorGeneratorReverse);
export const Wands = generateMinors('wands', 37, doorGenerator);
export const Swords = generateMinors('swords', 51, doorGenerator);
export const Pentacles = generateMinors('pentacles', 65, doorGeneratorReverse);