import React from 'react';
import {getImgBaseUrl, getDoorUrl} from '../services/url';
import './card.css';

export const TarotCard = ({name, img, link}) => {
  return (
    <a href={getDoorUrl(link)} alt={name | img} className="card">
      <img src={`${getImgBaseUrl()}tarot-${img}.jpg`} alt={name} className="card__image" />
    </a>
  );
};